:root {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

*, :after, :before {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ul, input, button {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__menu {
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  transition: color .7s;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  display: block;
}

.nav__link:not(.nav__link--first) {
  margin-left: 20px;
}

.nav__link:hover {
  color: #00acdc;
  transition: color .3s;
}

.nav__link--is-active {
  color: #00acdc;
  position: relative;
}

.nav__link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  align-items: center;
  width: 200px;
  transition: transform .3s;
  display: flex;
}

.card__content {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  padding-top: 32px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__description {
  color: #060b35;
  flex-direction: column;
  margin: 0 16px;
  display: flex;
}

.card__description:hover {
  color: #34568b;
}

.card__description_info {
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description_code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rewiew {
  color: #060b35;
  letter-spacing: 0;
  justify-content: space-between;
  align-items: end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__rewiew_count {
  margin: 0;
}

.card__price {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.card__price_name {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

.card__price_value {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 17px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  transition: background-color .7s, color .7s;
}

.card:hover {
  transform: scale(1.2);
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}
